    <h3>Help Line</h3>\n                    <h5\n                        v-if=\"\n                            showForm == true ||\n                            contactMembers.length == 0 ||\n                            edit == true\n                        \"\n                    >\n                        Add supporting members to helpline for guests to contact\n                        or seek help directly to the incharge members\n                    </h5>\n                </div>\n                <a\n                    :href=\"route('hostmembers.index')\"\n                    v-if=\"\n                        contactMembers.length == 0 ||\n                        edit == true ||\n                        showForm == true\n                    \"\n                >\n                    <img src=\"/assets/user-plus.svg\" alt=\"\" />\n                </a>\n                <button @click=\"showForm = true\" v-else>\n                    <span>+</span>Add Help line\n                </button>\n            </div>\n            <div\n                v-if=\"\n                    contactMembers.length == 0 ||\n                    edit == true ||\n                    showForm == true\n                \"\n                class=\"help-line-add-form\"\n            >\n                <form @submit.prevent=\"submitForm\" class=\"form-row\">\n                    <div class=\"form-group-custom\">\n                        <label for=\"name\" class=\"form-label\">For</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            id=\"name\"\n                            required\n                            placeholder=\"Name\"\n                            name=\"name\"\n                            v-model=\"formValues.contact_for\"\n                        />\n                        <span\n                            v-if=\"errorSubmit && errorSubmit.contact_for\"\n                            class=\"errMsg\"\n                            >{{ errorSubmit.contact_for[0] }}</span\n                        >\n                    </div>\n                    <div class=\"form-group-custom\">\n                        <label class=\"form-label\">Name</label>\n                        <select\n                            name=\"category_id\"\n                            class=\"form-control\"\n                            v-model=\"formValues.host_id\"\n                        >\n                            <option\n                                v-for=\"(item, id) in members\"\n                                :key=\"id\"\n                                :value=\"item.id\"\n                            >\n                                {{ item.name }}\n                            </option>\n                        </select>\n                        <span\n                            v-if=\"errorSubmit && errorSubmit.host_id\"\n                            class=\"errMsg\"\n                            >{{ errorSubmit.host_id[0] }}</span\n                        >\n                    </div>\n                    <div class=\"form-group-custom\" style=\"text-align: right\">\n                        <button\n                            v-if=\"contactMembers.length > 0\"\n                            @click=\"closeForm\"\n                            type=\"button\"\n                            class=\"btn btn-sm close\"\n                        >\n                            <span>Close</span>\n                        </button>\n                        <button\n                            :disabled=\"disableForm\"\n                            type=\"submit\"\n                            class=\"btn btn-sm\"\n                        >\n                            <span>Submit</span>\n                        </button>\n                    </div>\n                </form>\n            </div>\n            <div v-else class=\"help-line-list-cont\">\n                <div\n                    v-for=\"(item, index) in contactMembers\"\n                    :key=\"index\"\n                    class=\"help-line-list-single\"\n                    @click=\"toggle($event)\"\n                >\n                    <span style=\"width: 20%\">{{ item.contact_for }}</span>\n                    <span style=\"width: 17%\" class=\"black\">{{\n                        item.member.name\n                    }}</span>\n                    <span style=\"width: 30%\" class=\"light\">{{\n                        item.member.email\n                    }}</span>\n                    <span style=\"width: 15%\" class=\"light\">{{\n                        item.member.mobile\n                    }}</span>\n                    <span style=\"text-align: right; width: 13%\">\n                        <img\n                            @click=\"\n                                editForm(\n                                    item.contact_for,\n                                    item.host_id,\n                                    item.id\n                                )\n                            \"\n                            src=\"/assets/edit.svg\"\n                            alt=\"\"\n                        />\n                        <img\n                            @click=\"deleteHelpline(item.id)\"\n                            src=\"/assets/trash.svg\"\n                            alt=\"\"\n                        />\n                    </span>\n                    <span\n                        ><i class=\"fas fa-chevron-down drop-arrow\"></i>\n                        <i class=\"fas fa-chevron-up drop-arrow\"></i\n                    ></span>\n                </div>\n            </div>\n            <flashMessage :message=\"message\"></flashMessage>\n        </div>\n    </div>\n</template>\n\n<script>\nimport flashMessage from \"../../../FlashMessage.vue\";\n\nexport default {\n    components: {\n        flashMessage,\n    },\n    props: [\"host\", \"invitation\", \"members\", \"contactList\"],\n    data() {\n        return {\n            formValues: {\n                contact_for: null,\n                host_id: null,\n            },\n            disableForm: false,\n            edit: false,\n            contactMembers: this.contactList ?? [],\n            errorSubmit: false,\n            showForm: false,\n            updateId: null,\n            message: null,\n        };\n    },\n    methods: {\n        submitForm() {\n            //console.log(invalid);\n            let _this = this;\n            _this.disableForm = true;\n            let meth = \"POST\";\n            let link = route(\n                \"hostinvitationcontact.store\",\n                _this.invitation.slug\n            );\n            if (_this.edit == true) {\n                meth = \"PATCH\";\n                link = route(\"hostinvitationcontact.update\", [\n                    _this.invitation.slug,\n                    _this.updateId,\n                ]);\n            }\n            axios({\n                method: meth,\n                url: link,\n                data: _this.formValues,\n            })\n                .then(function (response) {\n                    _this.edit = false;\n                    _this.showForm = false;\n                    _this.disableForm = false;\n                    _this.formValues = {};\n                    _this.contactMembers = response.data;\n                    _this.message = \"Helpline Member added!\";\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.disableForm = false;\n                    _this.errorSubmit = error.response.data.errors;\n                    if (_this.errorSubmit) {\n                        _this.message = \"Kindly correct the feilds!\";\n                    } else {\n                        _this.message = \"Please try again later!\";\n                    }\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                });\n        },\n        editForm(val, host, member) {\n            this.formValues = {\n                contact_for: val,\n                host_id: host,\n            };\n            this.updateId = member;\n            this.edit = true;\n            this.showForm = true;\n        },\n        closeForm() {\n            this.showForm = false;\n            this.edit = false;\n            this.formValues = {};\n            this.updateId = null;\n        },\n        deleteHelpline(id) {\n            let _this = this;\n            axios({\n                method: \"DELETE\",\n                url: route(\"hostinvitationcontact.destroy\", [\n                    _this.invitation,\n                    id,\n                ]),\n            })\n                .then(function (response) {\n                    _this.contactMembers = response.data;\n                    _this.message = \"Member Deleted!\";\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.disableForm = false;\n                    _this.errorSubmit = error.response.data.errors;\n\n                    _this.message = \"Please try again later!\";\n                });\n        },\n        toggle(e) {\n            //console.log(screen.width);\n            if (screen.width <= 968) {\n                let currEle = e.currentTarget;\n                let all = document.getElementsByClassName(\n                    \"help-line-list-single\"\n                );\n                if (currEle.classList.contains(\"active\")) {\n                    currEle.classList.remove(\"active\");\n                    return;\n                }\n                all.forEach(function (item) {\n                    if (item.classList.contains(\"active\")) {\n                        item.classList.remove(\"active\");\n                    }\n                });\n\n                currEle.classList.add(\"active\");\n            }\n        },\n    },\n    mounted() {\n        this.$nextTick(function () {});\n    },\n};\n</script>\n\n<style scoped>\n.errMsg {\n    color: red;\n    font-size: 10px;\n}\n.help-line-head {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    position: relative;\n}\n.help-line-head h3 {\n    font-family: \"Perpetua\";\n    font-style: normal;\n    font-weight: 700;\n    font-size: 24.4444px;\n    line-height: 28px;\n    text-align: center;\n    color: #333333;\n    margin-bottom: 22px;\n}\n.help-line-head h5 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 12px;\n    line-height: 18px;\n    text-align: center;\n    color: #000000;\n}\n.help-line-list-cont {\n    margin-top: 15px;\n}\n.help-line-head > button,\na {\n    outline: none;\n    border: none;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 11.4286px;\n    line-height: 17px;\n    color: #000000;\n    padding: 14px 20px;\n    position: absolute;\n    right: 0;\n    top: 0;\n    border: 1.14286px solid #e3e8ee;\n    border-radius: 5.71429px;\n    background-color: white;\n}\n.help-line-list-single {\n    border: 0.5px solid #d2d2d2;\n    border-radius: 6px;\n    padding: 17px 25px;\n    display: flex;\n    align-items: center;\n    margin-bottom: 12px;\n}\n.help-line-list-single > span {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12px;\n    line-height: 18px;\n    color: #c4456f;\n    margin-right: 5px;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n    overflow: hidden;\n    display: block;\n    margin-bottom: 5px;\n}\n.black {\n    color: #000000 !important;\n}\n.light {\n    font-weight: 300 !important;\n    color: #7b7b7b !important;\n}\n.form-group-custom > label {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 800;\n    font-size: 12px;\n    line-height: 8px;\n    margin-left: 10px;\n\n    color: #000000;\n}\n.form-group-custom > input,\n.form-group-custom > select {\n    background: rgba(239, 220, 226, 0.2);\n    border: 0.643349px solid #f7dae4;\n    border-radius: 7.72019px;\n    margin-bottom: 12px;\n}\n.form-group-custom > select {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    background-image: url(/assets/icon-down.svg);\n    background-repeat: no-repeat;\n    background-position: 98%;\n    background-size: 20px;\n}\n.form-group-custom > input::placeholder {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 10px;\n    line-height: 8px;\n    margin-left: 10px;\n\n    color: #000000;\n}\n.form-group-custom > button {\n    background: #c4456f;\n    border-radius: 7.72px;\n    display: inline-block;\n    padding: 11px;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 9.10182px;\n    line-height: 14px;\n    display: inline-block;\n    min-width: 104px;\n    text-align: center;\n    box-shadow: none !important;\n    color: #ffffff;\n}\n.close {\n    color: #828282 !important;\n    background: white !important;\n    border: 0.5px solid #afa5a5;\n}\n.drop-arrow {\n    display: none;\n    text-align: right;\n}\n.form-row {\n    width: 60%;\n    margin: auto;\n}\n@media only screen and (max-width: 996px) {\n    .form-row {\n        width: 100% !important;\n    }\n    .help-line-list-single .fa-chevron-down.drop-arrow {\n        display: block;\n    }\n    .help-line-list-single.active .fa-chevron-down.drop-arrow {\n        display: none;\n    }\n    .help-line-list-single.active .fa-chevron-up.drop-arrow {\n        display: block;\n    }\n    .help-line-list-single {\n        flex-wrap: wrap;\n        justify-content: space-between;\n    }\n    .help-line-list-single > span:nth-child(1) {\n        width: 42% !important;\n        order: 1;\n    }\n    .help-line-list-single > span:nth-child(2) {\n        width: 42% !important;\n        order: 2;\n        text-align: right;\n    }\n    .help-line-list-single > span:nth-child(6) {\n        width: 6% !important;\n        order: 3;\n    }\n    .help-line-list-single > span:nth-child(3) {\n        width: 100% !important;\n        order: 4;\n        display: none;\n    }\n    .help-line-list-single > span:nth-child(4) {\n        width: 45% !important;\n        order: 5;\n        display: none;\n    }\n    .help-line-list-single > span:nth-child(5) {\n        width: 45% !important;\n        text-align: right;\n        order: 5;\n        display: none;\n    }\n    .help-line-list-single.active > span:nth-child(3),\n    .help-line-list-single.active > span:nth-child(4),\n    .help-line-list-single.active > span:nth-child(5) {\n        display: block !important;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
